.BriefBlog{
    margin: 30px 0 0 0;
    padding:5px 0;
    transition: transform 0.3s;
    width: 100%;
    height:  200px;
    background-color: white;
    border-radius: 10px;
    box-shadow: 10px 10px 15px  #bbb;
}
.BriefBlog:hover{
    transform: scale(1.05);
}

.bbtitle{
    font-size: 20px !important;
    color: black;

}
.BriefBlog .bbimag{
    float: left;
    margin-top:20px;
    width: 120px;
    height: 120px;
    margin-left: 5%;
    overflow: hidden;
    border-radius: 5px;
    /* background-color: red; */

}
.BriefBlog .bbimag img{
    width: 100%;
    
}
.BriefBlog .bbContent{
    float: right;
    width: 65%;
    height: 80%;
    margin-right: 5%;
    font-family: -apple-system,BlinkMacSystemFont,Helvetica Neue,PingFang SC,Microsoft YaHei,Source Han Sans SC,Noto Sans CJK SC,WenQuanYi Micro Hei,sans-serif;
    overflow: hidden;
    text-indent: 2em;
    font-size: 16px;
}
.BriefBlog .bbContent h1{
    font-size: 25px;
    margin-bottom: 10px;

}

.bbbottom{
    float: left;
    width: 100%;
    height: 20%;
    padding-top: 5px;
   
}
.bbbottom .viewtimes{
    float: left;
    margin:0 20px;
    
}
.bbbottom .updatetimes{
    float: left;
    margin:0 20px;
}
.bbbottom .bbcatogery{
    float: left;
    width: 40px;
    height: 50%;
    padding-left: 4px;
    border-radius: 30%;
    background-color: #e7cd79;
    color: #4285f4;
    
}
.bbbottom .readmore{
    float: right;
    margin-right: 20px;    
}
.bbbottom .readmore img{
    margin-left: 5px;
}
@media screen and (max-width: 375px) {
    .bbtitle{
        font-size: 16px;
    }
    .bbcontent {
        display: none;
    }
}

