.abimg{
    width: 100%;
    height:300px;
    overflow: hidden;

}
.abimg img{
    width: 100%;
    margin-top: -700px;
    z-index: -1;
}

.tellsomething{
    position: absolute;
    top: 20%;
    right: 10%;
    color: white;

    animation: trans 1s ;
}
.tellsomething h1{
    color: white;
}

@keyframes trans {
    from{
        right: 0%;  

    }
    to{
        right: 10%;
    }
}

.aboutme{
    overflow: hidden;
}
.aboutme .left{
    width: 30%;
    height: 400px;
    padding-top: 100px;
    box-sizing: border-box;
    float: left;
    animation: show 1s;

}
.aboutme .left h1{
    color: #111211;
}
.aboutme .left img{
    height: 100px;
    margin-bottom: 10px;
}

.aboutme .right{
    float: right;
    width: 70%;
    overflow: hidden;
    height: 400px;
    padding: 100px 30px 0 0 ;
    box-sizing: border-box;
    animation: show 1s;
    

    /* background-color: blue; */

}
.aboutme .right p{
    text-align: left;
}
.aboutme .right a{
 
    float: left;
    
    text-indent: 2em;
}

@keyframes show {
    from{
        width: 10%;
    }
}
